export default (state = initialState, action) => {
  switch (action.type) {
    case "ADD":
      state = {
        ...state,
        total: state.total + action.num,
        totalPrice:
          state.totalPrice + state.items[action.title].price * action.num,
        items: {
          ...state.items,
          [action.title]: {
            ...state.items[action.title],
            num: state.items[action.title].num + action.num,
          },
        },
      };
      break;
    case "SET":
      state = {
        ...state,
        total: state.total + action.num,
        totalPrice:
          state.totalPrice + state.items[action.title].price * action.num,
        items: {
          ...state.items,
          [action.title]: {
            ...state.items[action.title],
            num: action.num,
          },
        },
      };
      break;
    case "SUB":
      state = {
        ...state,
        total: state.total - action.num,
        totalPrice:
          state.totalPrice - state.items[action.title].price * action.num,
        items: {
          ...state.items,
          [action.title]: {
            ...state.items[action.title],
            num: state.items[action.title].num - action.num,
          },
        },
      };
      break;
    case "ZERO":
      state = {
        ...state,
        total: state.total - state.items[action.title].num,
        totalPrice:
          state.totalPrice -
          state.items[action.title].num * state.items[action.title].price,
        items: {
          ...state.items,
          [action.title]: {
            ...state.items[action.title],
            num: 0,
          },
        },
      };
      break;
    case "RESET":
      state = initialState;
      break;
  }

  return state;
};
